    <div id="自动驾驶解决方案"  class="message-body">
      <b>3.1 自动驾驶解决方案</b>
      <br/>
      <br/>
     自动驾驶涵盖多种解决方案。非全栈解决方案主要针对单独的技术或产品提供服务；全栈解决方案需要全部掌握自动驾驶领域最核心最关键的技术，从数据获取、底层算力、软硬件开发等层面形成闭环，实现车端、路端、云端融合一体化发展，拥有更好的性能和更强的可控性。一站式解决方案除了具备全栈解决方案的技术优势外，还能够提供前中后端一体化的运营服务，构建一站式大生态，打造更深的护城河。      </div>
  </article>
