  <article class="message is-link">
    <div id="自动驾驶汽车的传感器"  class="message-body">
      <b>3.2 自动驾驶汽车的传感器</b>
      <br/>
      <br/>
      人口增长导致车辆数量增加，对当前交通基础设施（如停车场、充电站和加油站）造成沉重负担。车辆数量的持续增加是导致交通问题包括空气污染、噪音污染、道路撞车和交通堵塞。为了解决这些问题，研究人员一直在开发AVS。目标是消除或至少减少由人为驱动因素引起的问题。
      <br/>
      <br/>
      AVS被定义为智能代理，可以通过安装在车辆上的不同传感器感知其环境。当人类使用感官（如视觉和听觉）驾驶时，AVS使用传感器（如摄像机和雷达）。传感器的质量在构建成功的AVS中起着关键作用。例如，如果从相应传感器收集的数据不可靠，则最佳感知算法的性能可能较差。
      <br/>
      <br/>
      传感器的作用是测量或检测环境的某些特性或这些特性随时间的变化。传感器大致分为两类：
      <br/>
      <br/>
      （a）外部感知传感器：它们记录车辆周围环境的特性（例如，摄像机和光探测与测距（LIDAR））；
      <br/>
      <br/>
      （b） 本体感觉传感器：它们记录车辆本身的特性（例如全球导航卫星系统（GNSS）位置和车轮里程表）。
      </div>
  </article>
  <div class='pic'>
      <img id="自动驾驶汽车上的传感器" src='../../assets/zdjsqcsdcgq.png' width="900" height="1000">
      <p>图：自动驾驶汽车上的传感器</p>
  </div>
  <article class="message is-link">
    <div class="message-body">
      自动驾驶中最常见和最广泛使用的传感器是摄像头。摄像机是一种被动的集光传感器，可以捕捉场景的丰富而详细的信息。摄像机的质量由各种指标决定，例如分辨率和视野。分辨率是创建图像并影响其质量的像素数。视野由摄像机可见的水平和垂直角度范围定义，可以通过镜头选择和变焦来改变。摄像机的动态范围由图像中最暗色调和最亮色调之间的差异定义。高动态范围对于自动驾驶车辆至关重要，因为在夜间驾驶时会遇到高度可变的照明条件。具有重叠视野和对齐图像平面的两个摄像机的组合称为立体摄像机；它们生成场景的视差图，用于估计每个像素的深度（Thakur，2018）。
      <br/>
      <br/>
      AVS中的第二个关键传感器是激光雷达。它发射光束。通过测量返回的光量和光束的飞行时间，可以估计反射物体的折射率。激光雷达生成三维点云地图，用于评估场景几何体。激光雷达通常由一些关键指标控制，如信源数量、每秒可收集的点数和视野（Bussemaker，2014）。
      <br/>
      <br/> 
      无线电探测和测距（雷达）传感器的使用时间比激光雷达长。它们能够强有力地检测环境中的大型物体，在恶劣天气下尤其有用，因为它们大多不受雨水影响。雷达传感器的特点是其探测范围、视野以及位置和速度测量精度（Curry，2005）。
      <br/>
      <br/>
      超声传感器在AVS中也很重要；他们用声波测量距离。它们特别适用于停车场景，在这种场景中，车辆需要靠近其他车辆移动（Curry，2005）。
      <br/>
      <br/>
      车载工业中的主要本体感知传感器之一是全球导航卫星系统，如GPS或伽利略。全球导航卫星系统可以测量车辆的位置、速度，有时还可以测量航向。惯性测量单元（IMU）测量车辆的角转速和加速度。另一个重要的本体感觉传感器是车轮里程表；它跟踪车轮的旋转速度，并使用这些信息来估计车辆的速度和航向变化率（Reinholtz et al, 2007）。
      <br/>
      <br/>
      通常，在识别环境后，AVS执行许多任务，包括目标检测、规划、决策、控制速度和驾驶，而无需任何人为干预。
    </div>
  </article>
