  <section id="RL-section" class="section is-medium">
    <h1 class="title">自动驾驶与强化学习</h1>
  </section>
  
  
  <div class="content">
    <keywords-reinforcement/>
    <keywords-deepreinforcement/>
  </div>
  <article class="message is-link">
    <div  class="message-body">
      尽管深度学习技术在自动驾驶中取得了令人鼓舞的成果，但是仍然远远不能实现自动驾驶汽车的完全自主，特别是在决策、运动规划和车辆控制方面。基于深度学习的技术有其固有的缺点，其中的主要缺点是需要数据集来学习预测模型，并且不能自校正累积误差。为了克服这些问题，研究人员正在尝试应用强化学习等其他技术，这些技术可以无需收集数据并且直接从环境中学习。强化学习模型能够通过不断的试错来学习如何执行目标任务。
      <br/>
      <br/>
      在这一部分，我们涵盖了现有的基于强化学习的方法，旨在解决自动驾驶问题，包括：(1) 决策；(2) 运动规划；(3) 车辆控制；(4) 自动驾驶汽车的社会行为。这些是自动驾驶应用的主要领域。
      <br/>
      <br/>
      在讨论强化学习在自动驾驶任务中的应用之前，我们简要回顾一下自动驾驶环境中的状态空间、动作空间和奖励机制。
    </div>
  </article>

  <div class="content">
    <publication/>
  </div>

  <article class="message is-link">
    <div id="状态空间、动作空间和奖励机制"  class="message-body">
      <b>5.1 状态空间、动作空间和奖励机制</b>
      <br/>
      <br/>
      为了成功地将强化学习应用于自动驾驶任务，设计适当的状态空间、动作空间和奖励函数非常重要。Leurent等人（2018）对自动驾驶研究中使用的不同状态和动作表示法进行了综合评述。
      <br/>
      <br/>
      自动驾驶汽车通常使用的状态空间特征包括：自身的位置、航向和速度，以及自车载传感器视野范围内的其他障碍物。为了避免状态空间维度的变化，通常会围绕自动驾驶汽车建立笛卡尔坐标系或极坐标系下的占用网格。这种操作进一步增加了车道信息，例如车道号、路径曲率、自身车辆过去和未来的轨迹；纵向信息，例如碰撞时间(TTC)；以及最后的场景信息，例如交通法规和信号位置。
      <br/>
      <br/>
      使用原始传感器数据，如相机图像、激光雷达、雷达等，会为我们提供更好的上下文信息，然而使用压缩的抽象数据大大降低了状态空间的复杂性。介于这两者之间的中等大小的状态空间表示，如2D鸟瞰图（BEV）是无法从传感器获得的，但是它仍然接近场景的真实空间状态。下图是自顶向下视图的图示，显示出了网格、过去和未来的轨迹以及关于场景的语义信息，例如交通信号的位置。这种状态空间表示保留了道路的空间布局，而基于图形的表示则无法保留。
    </div>
  </article>
  <div class='pic'>
      <img id="2D鸟瞰图" src='../../assets/2D.png' width="1000" height="1000">
      <p>图：2D鸟瞰图</p>
