  <article class="message is-link">
    <div class="message-body">
      自动驾驶汽车控制策略必须控制许多不同的执行器。用于车辆控制的连续值执行器包括转向角、节气门和制动器。其他执行器，例如齿轮的变化是离散的。为了降低复杂性并允许应用仅适用于离散动作空间（如DQN）的强化学习算法，可将连续值执行器（如转向角、节气门和制动器）的调节范围划分为一系列间隔区间大小相等的离散值，以将动作空间均匀离散化。此外，有人建议在对数空间进行离散化，因为实际驾驶中选择的转向角多数都靠近中心值。然而，离散化也有其自身的缺点，如果动作之间的步长值太大，可能会导致车辆运动轨迹不平稳或不稳定。此外，当选择用于执行器的离散值数量时，我们需要在具有足够的离散值以实现平滑控制和不具有太多离散值以提升控制效率之间权衡。作为离散化的替代方案，我们可以选择直接学习策略的DRL算法来处理连续值执行器（例如DDPG）。时间抽象选项框架（Sutton等人，1999）也可以用来简化选择动作的过程，该框架选择options而不是某个基本动作。这些options代表着一个子策略，它可以将一个基本动作扩展到多个时间步长。
      <br/>
      <br/>
      为应用于自动驾驶技术的强化学习算法设计奖励函数仍然是一个悬而未决的问题。为自动驾驶任务设计的奖励标准包括：朝向目的地行进的距离、自我车辆的速度、使自我车辆保持静止、与其他道路使用者或场景物体的碰撞、人行道上的违规行为、保持在车道上、以及在避免极端加速、制动或转向和遵守交通规则的同时保持舒适和稳定性。
    </div>
  </article>

