  <article class="message is-link">
    <div id="模拟器和场景生成工具"  class="message-body">
      <b>5.2 模拟器和场景生成工具</b>
      <br/>
      <br/>
      自动驾驶数据集中的训练集包含不同场景的图像和标签对，我们能够利用它来实现监督学习。强化学习算法需要一个特殊环境，其中“状态-动作对”需要是可恢复的，同时需要分别对车辆状态、环境以及环境和主体的运动和动作的进行随机性建模。
      <br/>
      <br/>
      各种模拟器被广泛地用于训练和验证强化学习算法。各种高保真感知模拟器能够模拟照相机、激光雷达和雷达，部分模拟器也能够提供车辆状态信息和运动信息。Rosique等人（2019）为读者提供了自动驾驶领域中使用的传感器和模拟器的完整综述。
      <br/>
      <br/>
      在现实世界中进行昂贵的评估之前，自动驾驶算法所学习的驾驶策略需要先在模拟环境中进行测试。Cutler等人（2014）提出了一种多保真度强化学习（MFRL）框架，该框架让我们能同时使用多个模拟器。在MFRL框架中，一系列保真度越来越高的模拟器被用于表示状态动力学（以及计算成本），这提高了我们训练和验证强化学习算法的能力，同时让我们能够以更少的代价找到与真实世界最为接近的策略。
      <br/>
      <br/>
      CARLA Challenge 是German Ros等人（2019）年推出的一个基于CARLA模拟器的自动驾驶任务竞赛，其中具有国家公路交通安全管理局报告中描述的碰撞前场景。该竞赛系统在关键场景中对自动驾驶算法进行评估，例如：自身车辆失去控制、自身车辆对视野外的障碍物做出反应、变道以避开缓慢的前方车辆等。自动驾驶算法的得分为在不同赛道上行驶的总距离的一个函数，并且违法行为将会扣除部分总得分。
    </div>
  </article>
  
  <div class="content">
    <number-papers1/>
  </div>

  <article class="message is-link">
    <div id="强化决策"  class="message-body">
      <b>5.3 决策</b>
      <br/>
      <br/>
      通过从经验和环境中学习，强化学习技术目前正在为自动驾驶汽车提供更好和更精确的信息。
      <br/>
      <br/>
      You等人（2018）提出马尔可夫决策过程（MDP）对自动驾驶汽车与周围车辆的相互作用进行建模。MDP允许自动驾驶汽车在超车或尾随另一辆车时，根据道路结构做出适当的决定。不过，他们的方法仍需要在不同的场景中进行更多的测试来验证。
      <br/>
      <br/>
      Hoel等人（2018）提出了一种在高速公路上做出决策（如变道、加速和制动）的方法。他们利用Deep Q Network (DQN)来训练他们的提案并预测正确的决策。不过，他们的方法并不能在其他情况下保证安全性（如环形路和十字路口）。
      <br/>
      <br/>
      无信号交叉口被认为是自动驾驶汽车做出准确且及时决策的最具挑战性的场景之一。Isele等人（2018）提出了一种基于DQN的方法来导航自动驾驶汽车安全的通过交叉口。他们提案的结果优于传统方法（Hafner等人，2013；Alonso等人，2011）。
      <br/>
      <br/>
      Okuyama等人（2018）将卷积神经网络和强化学习相结合，在具有车道标志和静态障碍物(例如人、停止的车辆)的模拟环境中训练自动驾驶汽车。CNN利用由自动驾驶汽车的前置摄像头拍摄的图像，以提取道路的主要状态特征。这些特征被反馈给DQN以预测下一步的行动。不足的是，他们的方法没有考虑动态障碍。
      <br/>
      <br/>
      Hoel等人（2019）提出了一种策略决策方法，该方法结合了蒙特卡罗树搜索（MCTS）和强化学习算法，以在两种高速公路驾驶情况下控制自动驾驶汽车（包括连续高速公路驾驶和退出高速公路）。他们的提案在模拟高速公路环境中进行了测试，初步结果显示了RL和MCTS相结合的有效性。
      <br/>
      <br/>
      Hoel等人（2020）针对三车道的单向高速公路，提出了另一种用于自动驾驶汽车的策略决策方法。他们使用强化学习来估计驾驶行为，例如加速、留在车道上、左转、右转和刹车。结果表明，随着时间的推移，他们的方法学会了做出有效的决策。
      <br/>
      <br/>
      Ye等人（2019）研究了一种强化学习方法，允许自动驾驶汽车在一条直行道路上进行车道变更(无信号灯控制)，该算法已经在模拟环境中利用Q学习进行了训练。然而，他们的方法需要在复杂的场景中进行更多的测试。
      <br/>
      <br/>
      Sun等人（2020）提出了一种基于强化学习的针对重型自动驾驶汽车的决策方法，即深度确定性策略梯度（DDPG）算法。该网络接收自动驾驶汽车的信息状态（包括速度、与其他车辆的距离）然后做出决定。模拟结果显示，他们的方法学会了通过环境快速做出决定。
      <br/>
      <br/>
      Duan等人（2020）提出了一种方法来训练自动驾驶汽车从四周环境中学习并做出三种决策（包括车道变换、右车道变换和左车道变换）。然而，他们只关注高速公路的情况，而没有处理其他情况（如交叉路口和城市交通）。
      <br/>
      <br/>
      Chen等人（2019a）提出了一种在车辆密集的环形交叉口上控制自动驾驶汽车的方法。他们的方法从传感器获取周围每个物体的信息（例如速度和位置）；该方法使用DQN预测正确的动作。他们在Carla模拟器中模拟了他们的算法，模拟结果显示出该方法在解决环形交叉口驾驶情况的乐观前景。
      <br/>
      <br/>
      Wolf等人（2017）使用DQN在模拟环境中训练自动驾驶汽车。利用摄像头，自动驾驶汽车识别道路并采取转弯动作（例如，左、半左、直、半右和右）。然而，他们的建议需要考虑到静态和动态的障碍。
      <br/>
      <br/>
      Likmeta等人（2020）提出了针对三种场景（变道、交叉路口、环形交叉口）的决策方法。他们使用强化学习来训练自动驾驶汽车在不同的情况下做出决策。 
      <br/>
      <br/>
      Huang等人（2019b）提出了一种基于强化学习的方法，通过利用状态信息（例如，车辆速度和道路距离）训练自动驾驶汽车预测所需的决策，以采取动作（加速、减速和转向）。
      <br/>
      <br/>
      Hu等人（2019）提出了一种基于强化学习的方法，在两个场景的融合中控制自动驾驶汽车的决策方法。他们的方案在所有测试场景中实现了零碰撞。然而，这种方法并不能保证安全，特别是在机动性较高的实际交通中。
      <br/>
      <br/>
      Wang等人（2020）提出了一种在复杂高速道路中生成驾驶策略的方法。基于反馈给DQN的传感器信息（例如，车辆位置和速度）来学习如何为复杂高速公路中的每辆车辆做出决策（加速、减速、保持车道、右转和左转）。模拟结果表明，该方法提高了道路的安全性。
      <br/>
      <br/>
      Zhou等人（2021）提出了一种混合交通中自动驾驶车辆协同变道的多智能体强化学习（MARL）框架。其中每一辆自动驾驶汽车根据相邻的自动驾驶汽车和人类驾驶汽车的运动做出车道变换决策。该方法提出了一种多目标奖励函数，以结合燃油效率，驾驶舒适性和自动驾驶的安全性。实验结果表明，该方法在效率、安全性和驾驶员舒适度方面均优于现有常见方法。
    </div>
  </article>
