  <article class="message is-link">
    <div id="强化运动规划"  class="message-body">
      <b>5.4 运动规划</b>
      <br/>
      <br/>
      Nosrati等人（2018）提出了一种基于强化学习的分层方法，使用DQN在多车道的道路上控制自动驾驶汽车。该方法的目标是安全地避开障碍物(如其他车辆和摩托车)。这项工作还需要在复杂的场景中进行测试(例如，城市交通和十字路口)。
      <br/>
      <br/>
      Paxton等人（2017）研究了一种基于强化学习的蒙特卡罗树搜索（MCTS）方法，用以生成长期运动规划。该方法主要是让自动驾驶汽车避免在交叉路口发生碰撞。然而，该方法还需要在其他复杂的环境中进行更多的测试。
      <br/>
      <br/>
      Zhu等（2018）模拟了自动驾驶汽车在与环境交互作用下的学习过程。基于来自四个摄像机视图的数据，结果表明强化学习算法能够适应不同的交通状况。
      <br/>
      <br/>
      You等人（2019）对自动驾驶汽车和高速公路交通之间的相互作用进行了建模，并考虑了道路几何结构。基于Q学习，自动驾驶汽车从环境中学习动作（例如，车道变换、速度保持、加速和制动）。仿真结果表明，该方法能够适应多车道多车辆的交通状况。
      <br/>
      <br/>
      Fayjie等人（2018）提出了一种运动规划方法，用于在两种场景下控制自动驾驶汽车（包括城市交通和五车道的高速公路）。该方法使用CNN提取来自相机和激光雷达的数据特征。这些特征被输入给DQN以估计正确的动作（例如，继续前进、向左、向右、加速和刹车）。然而，该方法没有考虑到其他道路参与者。
      <br/>
      <br/>
      Chen等人（2020a）提出了一种可解释的端到端方法来解决复杂城市场景中的自动驾驶问题(交叉口和环形交叉口)。强化学习模型将相机和激光雷达的图像作为输入，并避免密集交通中的碰撞。实验结果表明，该方法优于其他强化学习方法（Mnih等人，2015；Lillicrap等人，2015年；Fujimoto等人，2018；Haarnoja等人，2018)。
      <br/>
      <br/>
      Cao等人（2020）提出了一种基于强化学习的高速公路出口规划方法。该方法基于对环境的观察来估计离开高速公路的预期。结果显示，该方法将离开高速公路的成功率增加了5%到50%。
      <br/>
      <br/>
      Chen等人（2020c）提出了一种基于DQN控制自动驾驶汽车的方法。CNNs和LSTM一起用于从摄像机图像中提取状态信息。这些特征被输入到DQN以学习运动命令，例如直行、右转和在十字路口左转。仿真结果表明，与DDQN方法相比该方法学习速度更快（Wang等，2016）。然而，该方法没有考虑障碍物（例如，行人、骑自行车的人和其他车辆）。
      <br/>
      <br/>
      即使深度学习能够从专业驾驶员的停车数据中学习，人类的知识也不能保证高效停车。为此，Zhang等人（2020a）提出了一种强化学习方法，使用MCTS来训练自动驾驶汽车学习停车策略。该方法实现了高效停车。
      <br/>
      <br/>
      Wang等人（2020a）提出了一种运动规划方法，该方法检测车辆轨迹中的障碍物，并使用强化学习方法自主地避开它们。他们在多车道和不同交通密度的道路上训练了他们的方法。
      <br/>
      <br/>
      Lu等人（2020）利用MDP来实现自动驾驶汽车处理复杂场景（包括没有交通信号的左转和多车道合并）。该方法的目标是训练自动驾驶汽车以实现在这两个场景中成功导航。实验结果证明了该方法的有效性和高效性。
      <br/>
      <br/>
      Kasra等人（2021）提出了一种基于强化学习的方案，用于解决存在不确定性的情况下进行自动驾驶汽车的运动规划。该方案专注于解决传感和感知的不确定性，这是由有限的视野、遮挡和感知范围引起的。该方案应用于两种不同的强化学习算法用于验证，Soft Actor-Critic和DQN。与传统的强化学习算法相比，该方法产生了更好的运动规划行为。
      <br/>
      <br/>
      Tung等人（2022）提出了一个基于学习的规划器DriveIRL，该规划器使用逆强化学习（IRL）在密集的城市交通中驾驶汽车。该规划器生成一组不同的轨迹建议，使用轻量级的且可解释的过滤器筛选这些轨迹，之后使用学习的模型对剩余的每个轨迹进行评分，最佳轨迹由自动驾驶汽车的低级控制器进行跟踪。他们在真实数据集上训练评分轨迹模型，并且在交通繁忙的拉斯维加斯大道进行了自动驾驶展示。
    </div>
  </article>
  <div class='pic'>
      <img id="强化学习运动规划方法" src='../../assets/rlydghff.png' width="900" height="1000">
      <p>表：强化学习运动规划方法</p>
  </div>
  

  <article class="message is-link">
    <div id="强化车辆控制"  class="message-body">
      <b>5.5 车辆控制</b>
      <br/>
      <br/>
      强化学习的目标是找到一个最佳的控制指令(例如，改变速度，刹车，或加速) ，通过一种迭代的方法探索环境。环境根据自动驾驶汽车当前的行为来奖励并纠正未来的错误。
      <br/>
      <br/>
      Li 等人（2017）提出了一种在多车道高速公路上控制自动驾驶汽车行驶的方法。该方法使用强化学习执行三个控制命令（减速、硬减速和维护）。
      <br/>
      <br/>
      Li 等人（2019a）将感知模块和控制模块相结合。感知模块以道路图像作为CNNs的输入提取状态信息特征。这些特性被输入给DQN来从环境中学习转向控制。该方法在不同的轨道上进行了测试，初步结果显示自动驾驶汽车学会了有效的控制。
      <br/>
      <br/>
      Zhu等人（2020）提出了一种基于深度确定性策略梯度（DDPG，lillicrap 等，2015）控制自动驾驶汽车速度以避免碰撞的强化学习方法。仿真结果表明了该方法在安全性和舒适性方面的有效性。
      <br/>
      <br/>
      Amini 等人（2020）提出了一种强化学习方法，用于在不同的天气条件（晴天和雨天)、光照情况(白天和夜晚)和道路类型(乡村和高速公路)下控制自动驾驶汽车。自动驾驶汽车会观察物体（例如树木、汽车和行人），并且主动绕开它们。仿真结果表明了该方法在实际道路上的自适应能力。
      <br/>
      <br/>
      Guo等人（2020）提出了一种基于强化学习的方法，用于在三车道高速公路中横向控制自动驾驶汽车。该方法的目标是安全地实现变道。结果表明，该方法改善了交通流量和交通容量。
      <br/>
      <br/>
      Wu等人（2020）研究了一种差分变速极限（DVSL）方法，用于在五车道高速公路中控制自动驾驶汽车的速度。DVSL 被建模为一个MDP问题，并且利用SUMO模拟器来训练自动驾驶汽车，使其从与环境的相互作用中学习。试验结果表明，该方法提高了高速公路行驶的安全性。
      <br/>
      <br/>
      Chen等人（2020b）提出了一个使用蒙特卡罗树搜索（MCTS）的强化学习方法，用于控制自动驾驶汽车执行不同的机动以避免碰撞。他们将控制过程建模为MDP问题，并使用MCTS来产生转向角。该方法显示了更高的控制稳定性和更高的避免意外事件的成功率。
      <br/>
      <br/>
      Zhang等人（2018）研究了一种基于DQN和Double-Q学习的自动驾驶汽车速度控制方法。他们用来自真实世界的数据训练该方法，结果显示该方法在价值准确性方面有所改进。 
      <br/>
      <br/>
      Baheri等人（2020）提出了一种在城市驾驶中保持车道的方法。他们的建议是从环境中提取状态观测值，并使用强化学习在CARLA模拟器中训练自动驾驶汽车。他们在两个城镇和不同的天气条件下对自动驾驶汽车进行了模拟，并且成功地完成了车道保持任务。
      <br/>
      <br/>
      Bouton等人（2020）使用强化学习和博弈论解决密集交通中的机动问题。该方法被建模为MDP问题，以实现自动驾驶汽车在合并场景中保持或改变车道。结果表明，与现有方法相比，该方法能够更有效地学习。
      <br/>
      <br/>
      Ye等人（2020）提出了一种高速公路自动换道策略，该策略使用了最近策略优化（PPO）和强化学习。利用车辆和周围车辆的状态，自动驾驶汽车学习如何避免碰撞和实现平稳机动。测试结果表明，该方法可以学习如何有效、安全地执行车道变换操作。
      <br/>
      <br/>
      Toromanoff等人（2020）提出了一种强化学习方法来解决复杂情况（包括车道保持、行人和车辆避让）。他们使用CARLA模拟器来训练模型，只使用一个摄像头来观察周围环境以实现城市驾驶。他们使用DQN来训练自动驾驶汽车从如何处理以前的情况中学习。仿真结果表明，该方法可以推广到未知环境。
      <br/>
      <br/>
      类似地，Jaritz等人（2018）仅使用一个摄像头来观察环境，利用一种强化学习结构（Mnih等人，2016）让自动驾驶汽车学习如何估计控制命令（转向、加速、制动）。他们用不同的道路结构（例如，转弯和山丘）、图形学（例如，季节和位置）和物理学（例如，道路附着力）来处理轨迹。然而，该方法没有考虑碰撞。
      <br/>
      <br/>
      Wang等人（2018）研究了在单向三车道的高速公路上控制自动驾驶汽车进行车道变换的问题。该方法考虑到了周围车辆的行为，并使用Q学习来实现车道变换。实验结果显示了该方法在学习车道变换方面的乐观前景。
      <br/>
      <br/>
      Liang等（2018）提出了一种可控模仿强化学习(CIRL)方法，利用DDPG方法（Lillicrap等人，2015）和CARLA模拟器在复杂场景中（车辆和行人）控制自动驾驶汽车。该方法的结果优于现有的强化学习技术（Dosovitskiy等人，2017）。
      <br/>
      <br/>
      Shi等人（2022）提出了一种基于双边控制模型（BCM）的深度强化学习（DRL）框架，用于实现优于人类驾驶策略的汽车跟随。该方法同时考虑前后车辆，并将双边信息集成到状态和奖励函数中。此外，该方法使用分散的多智能体强化学习框架为每个智能体生成相应的控制动作。仿真结果表面，该方法学习的策略优于人类驾驶策略。
      <br/>
      <br/>
      Edoardo等人（2022）提出了一种基于强化学习的方法，用于控制自动驾驶赛车实现稳健的循迹驾驶。该方法受到上层运动规划器所生成的轨迹的限制。该方法的利用强化学习的启发式特性，同时利用传统规划方法中分层控制结构的可靠性。模拟实验表明，该方法实现了更低的碰撞概率，并且实现了比端到端方法更低的单圈时间。
    </div>
  </article>
  <div class='pic'>
      <img id="强化学习车辆控制流程" src='../../assets/rlclkzlc.png' width="700" height="1000">
      <p>图：强化学习车辆控制流程</p>
  </div>

  <article class="message is-link">
    <div id="强化社会行为"  class="message-body">
      <b>5.6 车辆社会行为</b>
      <br/>
      <br/>
      任何自动驾驶模型系统都必须能够实时处理城市交通中的道路使用者行为。在这方面，强化学习方法证明了其在理解道路使用者行为并做出正确决策的能力。
      <br/>
      <br/>
      Saleh等人（2018）提出了城市交通环境中的行人意图预测问题。基于对行人过去轨迹的观察，他们的方法使用循环神经网络和强化学习来预测行人的未来行为。实验结果显示，该方法显著改进了预测效果。
      <br/>
      <br/>
      Li等人（2020a）提出了一种基于强化学习的方法，使用MDP识别行人行为并避开行人。该方法考虑了车辆和行人都在同一条结构化双车道道路上的情况。车辆在道路上行驶，而一名行人正在等待过马路，这种情况可能会造成事故，该方法的目标是避免这种碰撞。
      <br/>
      <br/>
      Li等人（2020b）提出了一种在复杂场景下估计未来行人位置的强化学习方法。该方法的目标是训练自动驾驶汽车学会识别道路上每个行人的未来姿态。该方法的结果在预测精度方面优于现有的方法（Van den Berg等人，2008；Chen等，2017b；Everett等人，2018；Gupta等人，2018）。
      <br/>
      <br/>
      Nasernejad等人（2021）将交叉口行人和自动驾驶汽车之间的相互作用建模为MDP问题。该方法能够让自动驾驶汽车根据行人在环境中的行为来避开行人。然而，该方法仅限于行人和车辆的相互作用，而其他相互作用(例如自行车)也应该被考虑。
      <br/>
      <br/>
      Wang等人（2021）提出了一种基于协商感知运动规划的强化学习框架。该框架采用强化学习来调整运动规划器的驾驶风格，通过动态修改运动规划器的预测视野长度，自适应地应对复杂环境中的不同事件。该框架将自动驾驶汽车与其他交通参与者之间的交互建模为马尔可夫决策过程。他们将该方法应用在模拟和现实世界中的狭窄车道导航，结果现实该方法优于常见的其他方案。
      <br/>
      <br/>
      Britod等人（2022）提出了一种在密集交通场景中的交互感知模型。该方法通过深度强化学习模型来学习一种交互感知策略，并用于推理道路上其他车辆的驾驶意图，进一步指导自身车辆的动态规划。该方法在两种模拟环境中（高速公路合并和无保护的左转弯）进行了实验。结果表明，该方法减少了碰撞次数，并提高了成功率。
    </div>
  </article>
  

