<template>
  <section id="DL-section" class="section is-medium">
    <h1 class="title">自动驾驶与深度学习</h1>
    <br/>
    <h2 class="subtitle">
    针对自动驾驶的六大主要任务，即场景理解、运动规划、决策、车辆控制、社会行为和通信。</h2>
  </section>
  
  <div id="深度场景理解" >

  <article class="message is-link">
    <div  class="message-body">
      <b>4.1 场景理解</b>
      <br/>
      <br/>
      在过去二十年中，场景理解领域使用有效的分布式语言技术取得了重大进展。这一进展使得通过使用不同的传感器（如激光雷达、摄像机和雷达）为AVS提供有关驾驶环境的关键和精确信息成为可能。事实上，深度CNN在实时图像分类和检测方面已显示出显著的结果（Fernandes et al., 2021）。如图4所示，场景理解中DL技术的使用为AVS提供了可操作的信息，如车道、交通灯、行人、交叉线、交通标志等的检测。Cordts et al. (2016)。 
      <br/>
      <br/>
      也就是说，感知仍然是车辆识别道路几何形状的主要先决条件之一, 并识别道路用户，如行人、自行车和其他车辆。感知分为两组：道路场景和目标检测。      
    </div>
  </article>
  <div class='pic'>
      <img id="场景理解图片" src='../../assets/mbjc.webp' width="900" height="1000">
      <p>图：场景理解</p>
  </div>
  <article class="message is-link">
    <div id="深度道路场景"  class="message-body">
      <b>4.1.1 道路场景</b>
      <br/>
      <br/>
      准确识别和提取主要道路信息对于AVS识别道路要素（例如路面、沟渠、护栏和栅栏）至关重要。由于DL的强大功能，可以实现高精度的道路要素识别。例如，Balado et al. (2019)使用移动激光扫描（MLS）获得的信息来识别道路环境的主要元素。他们的提议基于点网（Qi et al., 2017）和语义分割（SS）方法来识别道路元素；它为城市地区的AVS提供了良好的前景。
      <br/>
      <br/>
      识别道路的几何结构可以为AVS提供有关其环境的更多信息。Laddha et al.(2016)用基于监督和非监督学习的混合算法，使用CNN识别道路几何形状。虽然该算法减少了人为标记的工作量，并使训练更具可扩展性，但仍需要在具有关键天气条件的不同场景中对其进行评估。
      <br/>
      <br/>
      全面了解周围环境和道路区域（例如遮挡、3D几何形状和道路拓扑类型）有助于自主驾驶的实际应用。
      为此，Yan et al. (2020)开发了一种基于激光雷达数据的多任务道路感知网络（LMRoadNet）方法。LMRoadNet旨在检测和估计道路测量值，如道路的长度和形状，以识别其拓扑结构。
      <br/>
      <br/>
      道路转角被认为是识别具有挑战性的区域之一。Bolte et al. (2019)的一项研究引入了道路转角的形式定义，并在AVS摄像机的视频信号中检测它们。因此，基于CNN的系统使AVS能够在关键情况下感知更好的道路。
      <br/>
      <br/>
      Tümen and Ergen (2020)开发了一种图像处理方法和基于DL的方法，用于使用CNN检测公路上的十字路口、分隔带和人行横道。鉴于这些地方的事故发生率很高，这种检测对于改善道路安全至关重要。
      <br/>
      <br/>
      Di et al. (2017)研究了交通场景理解问题，以从具有照明条件的图像中识别道路物体。他们考虑了基于CNN的密集对应迁移学习方法，以提取城市环境图像的深度表示。然而，这种方法没有考虑到不同和复杂的驾驶场景。
      <br/>
      <br/>
      交通标志的成功检测和分类是全自动驾驶汽车要克服的主要挑战之一。例如，Sajjad et al. (2020)开发了一种检测和避开障碍物的方法。该模型基于视觉传感器识别各种交通标志；它允许使用超声波传感器避开障碍物。作者实现了他们的原型，通过仅使用单目视觉传感器来促进车辆感知。
    </div>
  </article>

  <div class="content">
    <number-papers2/>
  </div>
  <article class="message is-link">
    <div id="深度目标检测"  class="message-body">
      <b>4.1.2 目标检测</b>
      <br/>
      <br/>
      准确、实时地检测周围物体，包括其他道路使用者，对于AVS来说至关重要。Wang et al. (2020b) 提出了一种基于DL技术的端到端三维目标检测方法。具体来说，他们的技术使用CNN和融合网络（FoFNet）来预测边界框和对象类别，例如汽车、行人和骑自行车的人。
      <br/>
      <br/>
      Prabhakar et al. (2017)提出了一种通过考虑天气条件来提高检测效率的方法。更具体地说，他们使用区域卷积神经网络（R-CNN）来识别和分类障碍物，如车辆、行人和动物。在雨天，他们使用在每个检测到的对象顶部带有类名的边界框。然而，需要对其他道路图像数据集进行更多测试，以评估该模型的准确性和适用性。
      <br/>
      <br/>
      除了目标检测之外，还需要估计其他关键参数（例如距离），以帮助实现自动驾驶。Chen et al. (2018b)提出多任务组合策略（CPMTL）算法，该算法旨在通过在检测过程中估计车辆与其他道路使用者之间的距离来改进检测。
      <br/>
      <br/>
      Li et al. (2021)解决了夜间使用CNN进行检测的问题，以改进微光图像识别。他们报告说，他们的提案可以帮助AVS识别道路，特别是在没有路灯的农村地区。
      <br/>
      <br/>
      Dinh et al. (2020)提出了一种基于CNN的方法，用于更好地检测城市交通中的目标；他们假设AVS配备了两个焦距不同的摄像机。他们表明，他们的提议允许检测小型物体（例如，小型和遥远的车辆）。
      <br/>
      <br/>
      现有的几种方法依赖视频进行目标检测；随着时间的推移，它们在连续的帧中对对象进行分类。然而，这没有考虑导致信息丢失的图像帧之间的空间和时间相关性。为了填补这一空白Liu et al.(2020) 基于运动辅助特征校准网络（MFCN）在两种情况下识别车辆，即遮挡和截断。与Ren et al. (2017), Song et al. (2018b), and Zhang et al. (2017)相比，他们的提案的评估确实表明，在不同的物体外观下，检测精度更好。
      <br/>
      <br/>
      由于来自传感器的数据量巨大，在训练过程中减少数据量可以提高检测模型的性能。基本上，只使用重要数据可以缩短基于DL的方法的训练时间。驾驶期间的时间至关重要，尤其是在紧急情况下；因此，检测必须是实时的（Das et al., 2020）。
      <br/>
      <br/>
      三维物体检测揭示了更准确的物体大小和位置信息，用于自动驾驶。自主驾驶的许多重要方面，如运动规划和车辆控制，通常需要忠实地表示车辆周围的三维空间。现有的3D物体检测贡献在使用传感器进行检测的方式上有着根本的不同。一些研究（Ma et al., 2019; Peng et al., 2020）仅使用单目摄像机。例如，Zhang et al. (2020b)提出了一种新的单目框架，用于自动驾驶场景中的三维检测；目标是使用3D盒检测和定位对象。
      <br/>
      <br/>
      其他几种方法提出了摄像机图像和激光雷达数据的融合。Chen et al. (2017a)试图通过开发基于多视图三维网络（MV3D）的三维物体检测方法来提高检测性能。他们的方案使用激光雷达点云和RGB图像来预测3D边界盒；它优于仅依赖传感器的方法。Hong et al. (2020) 提出了一种3D检测方法，该方法利用激光雷达点云和RGB图像作为交叉融合网络的输入，他们通过模拟表明，他们的提议允许检测通常仅使用传感器难以检测的对象。
      <br/>
      <br/>
      弱势道路使用者的安全仍然是一个具有挑战性的重大问题。因此，行人的检测至关重要，因为他们被视为主要的弱势道路使用者。在此背景下，Zhao et al. (2020)提出了一种称为行人位置感知网络（P-LPN）的方法；它基于内部级联网络（InCNet）。P-LPN将行人分为两组：在车道上移动的行人和在人行道上移动的行人。它使用区域建议网络（RPN）在语义地图上提供每个行人的位置。然而，P-LPN没有考虑行人的意图，这对于AVS理解行人的运动非常重要。
    </div>
  </article>
