<div id="深度运动规划" >
  <article class="message is-link">
    <div   class="message-body">
      <b>4.2 运动规划</b>
      <br/>
      <br/>
      在场景理解（或环境感知）之后，运动规划是AVS的下一个主要任务，以避免在不同环境中发生碰撞，从而安全平稳地导航。为了处理运动规划问题，传统方法（例如：Dijkstra算法、A-Star算法、State lattice算法）依赖于优化模型。这些算法在计算范围太大时速度缓慢，不适合实时应用。因此，许多研究人员提出了替代方法，例如基于DL的技术。这些方法分析感知信息，以便识别障碍，然后预测正确的动作。这使得AVS可以通过在时间和距离方面找到有效的路径来安全导航。
      <br/>
      <br/>
      我们将运动规划分为两类：运动命令预测和轨迹预测。运动命令预测主要包括计算转向角、加速度和/或制动，而轨迹预测包括计算所有道路参与者（包括ego-vehicle）的短期轨迹及其未来状态。
    </div>
  </article>
  <div class='pic'>
      <img id="深度学习运动规划流程" src='../../assets/dlydghlc.png' width="900" height="1000">
      <p>图：深度学习运动规划流程</p>
  </div>
  <article class="message is-link">
    <div class="message-body">
      <b>4.2.1 运动命令预测</b>
      <br/>
      <br/>
      由于交通状况的复杂性，很难建立一个通用的运动规划系统，尤其是实时运动规划系统。事实上，道路使用者的行为仍然不可预测，难以建模。例如，Bai et al. (2018)将CNN和LSTM结合起来提取时空信息，目的是基于真实数据实时预测转向角。Song et al. (2018a)使用从驾驶模拟器收集的数据，使用类似的方法估计转向角。
      <br/>
      <br/>
      Chen et al. (2018a)提出将CNN和LSTM结合起来进行规划决策。他们报告说，使用模拟和真实交通可以使AVS在许多情况下更好地模拟人类驾驶。然而，我们认为，应该更广泛地测试他们的方案，以评估和改进其性能。
      <br/>
      <br/>
      一些贡献（Bai et al., 2018; Song et al., 2018a; Chen et al., 2018a）只关注转向角估计；其他人确实解决了不止一个运动命令。例如，Hu et al. (2020)提出了一种基于CNN和LSTM的深度级联神经网络，以同时预测多个运动指令（即转向角、加速度和制动）。
    </div>
  </article>
  <article class="message is-link">
    <div class="message-body">
      <b>4.2.2 轨迹预测</b>
      <br/>
      <br/>
      AVS需要分析传感器收集的数据，以检测障碍物，从而安全导航到目的地。Zhao et al. (2019)提出了卡尔曼学习网络（LKN），以根据摄像机使用DL收集的数据估计车辆轨迹。Banzhaf et al. (2019)提出了一种基于CNN的方法，该方法使用收集的数据预测车辆未来的运动，同时考虑静态障碍物和无规则的道路。因此，在他们的方案中，双向RRT*被用以减少计算时间。
      <br/>
      <br/>
      Grigorescu et al. (2019)提出了一种改进AVS最优路径轨迹预测的方法。他们融合激光雷达和雷达数据来估计最佳轨迹。Cui et al. (2019)提出了一种估计其他车辆和行人轨迹的方法；他们使用CNNs处理来自激光雷达或雷达的数据。Djuric et al. (2020)使用类似的方法预测短期车辆轨迹和道路参与者（例如周围车辆）的未来状态。Deo and Trivedi (2018)提出了CNN和LSTM，以预测各种交通场景中周围车辆的未来轨迹。Zhang et al. (2020c)通过探索人类驾驶员在环境中的体验特征，使用模仿学习。它们利用CNNs处理3D环境信息，并提供周围车辆的状态。
      <br/>
      <br/>
      Leordeanu and Paraicu (2021)开发了一种方法，仅基于视频和最终目的地预测AVS的当前位置和未来轨迹。该方法可以适应不同天气条件下的短期和长期导航。
      <br/>
      <br/>
      Jeong et al. (2020)提出了一种基于LSTM和RNNs的运动预测算法；目标是减少十字路口的交通事故。该算法以周围车辆的状态信息为输入，预测目标状态序列。
      <br/>
      <br/>
      Rehder et al. (2018)使用完全卷积网络（FCNs）操作环境地图，以推断所有可能的步行目的地。仿真结果表明，该系统能够准确预测目的地和轨迹。
    </div>
  </article>
