  <article class="message is-link">
    <div id="深度决策"  class="message-body">
      <b>4.3 决策</b>
      <br/>
      <br/>
      自动驾驶的决策需要对环境进行准确而充分的描述。以往传统的方法(如启发式和数值优化)未能模拟所有可能的情况。而最近，深度学习技术已经显示出在实时和复杂环境下做出决策的能力。
      <br/>
      <br/>
      Li et al.(2018)利用cnn通过提取道路场景图像来模仿人类驾驶员，从而做出正确的驾驶决策。Gallardo等人(2017)在Krizhevsky et al. (2012)中提出了一种通过cnn，AlexNet架构的环境为自动驾驶汽车做决策的方法。Xie et al. (2019) 提出了一种利用LSTM对自动驾驶汽车与周围车辆之间的相互作用进行建模的变道方法
      <br/>
      <br/>
      自动驾驶汽车的变道机动仍然是一项复杂而富有挑战性的任务。因此，发展能够考虑到周围车辆运动的精确的变道系统是必要的 。Liu et al. (2019b)提出了一种基于dnn的方法，该方法受益于驾驶员的历史经验和V2V信息，以实现准确的变道机动。
      <br/>
      <br/>
      Strickland等人(2018)提出了一种在紧急情况下避免撞车的方法。为了实现这一目标，他们使用贝叶斯卷积LSTM来处理数据并避免现有的碰撞。Wang等人(2020c)使用r - cnn在环形路口引导自动驾驶汽车。他们的方案能够在自动驾驶汽车到达环形路口时做出决定(例如进入或等待)。
    </div>
  </article>
