  <div id="深度车辆控制"  class="message-body">
  <article class="message is-link">
    <div   class="message-body">
      <b>4.4 车辆控制</b>
      <br/>
      <br/>
      车辆控制负责纠正由运动规划和决策任务产生的错误。控制器在自动驾驶汽车中的作用是稳定和引导车辆沿着路径行驶。它接收由感知和定位模块估计的车辆状态。AVs根据这些状态选择合适的控制命令，如图6所示。
      <br/>
      <br/>
      在本节中，我们首先介绍经典的AV控制策略，这些策略允许最小化成本函数定义了一组状态和控制动作。这些传统控制器可以分为无模型和基于模型两类。
      <br/>
      <br/>
      无模型控制器:这种控制器不使用任何被控制系统的数学模型。它们允许根据设定值和当前状态之间的错误纠正操作。这些控制器很容易实现，因为它们不需要深入了解系统行为。
      <br/>
      <br/>
      然而，它们很难调优，而且不能保证最优性能，只能在有限的操作条件下良好的工作(Samak et al.， 2011)。这种控制器的一个例子是PID，它允许AVs以尽可能小的振动或抖动达到其目标状态。PID的输出依赖于三个超参数，每个超参数对应控制器缩写(P-I-D)。这些超参数特定于每个应用程序 (Kiong et al., 1999; Crenganis and Bologa, 2016)。
      <br/>
      <br/>
      Farag(2020)使用PID控制器控制自动驾驶汽车在路径规划器计算的轨迹上行驶。测试结果表明了PID在不同车速下的性能。 Alonso et al.(2013) 提出了一种基于二次函数最小化的PID控制器参数整定方法。他们的建议被应用于控制自动驾驶车辆跟随另一辆前面的车辆。
      <br/>
      <br/>
      基于模型的控制器:这些控制器使用数学模型来控制被控制系统。运动学控制器决定车辆的运动，忽略作用在系统上的内力或外力。特别是在加速度不显著的低速下，它们是有效的。Satouri等人(2021)使用了一个运动学模型的例子，例如自行车模型(Rajamani, 2011)，它允许控制非线性车辆的运动。该模型估计了汽车的速度和转向角，以及前、后轮胎的转向刚度。他们的方案通过CarSim模拟器进行了测试和验证，结果表明，该模型对运动规划给出的参考路径轨迹具有良好的跟踪性能。
      <br/>
      <br/>
      动态控制器考虑作用在系统上的力和力矩来估计车辆的运动。模型预测控制利用系统的线性或非线性运动模型预测其未来状态，并通过在每个时间步上求解一个优化问题来确定最优控制动作(Samak et al.， 2011)。
      <br/>
      <br/>
      Funke等人(2016)将模型预测和反馈控制器相结合，在保持稳定和避免碰撞的同时，估计到达预期目的地所需的转向角。
      <br/>
      <br/>
      Li等人(2017)提出了一种方法，允许控制自动驾驶车辆跟踪参考路径并避免碰撞(如车辆、骑自行车的人和行人)。他们的建议结合了前馈和反馈控制器(Kapania和Gerdes, 2015)来调整驾驶时的转向角度。Ni等人(2017)也使用前馈和反馈控制器来沿着期望的路径引导AV。他们的模型通过估计轮胎转向刚度来增强控制器的鲁棒性。
      <br/>
      <br/>
      以前的控制器需要深入了解过程和调优。此外，在实施合适的控制器之前，需要知道运动学或动力学模型。这些控制器只能用于特定的场景，而不适合其他情况。当场景发生变化时，它们需要重新调整。此外，他们没有考虑到每个道路使用者的行动，如其他车辆，骑自行车的人和行人。因此，在设计新的自动驾驶汽车相关方法时，考虑一般的vru，特别是行人就变得至关重要。这将有助于更好地理解和预测行人在自动驾驶汽车上路时的意图。
      <br/>
      <br/>
      值得注意的是，这些控制器不处理复杂的情况，如密集的交通，十字路口，人行横道等。它们只专注于控制自动驾驶汽车沿着运动规划产生的参考路径运动。由于这些原因，人工智能技术通过从环境中学习来获得精确的控制命令(例如，转向和调节速度)，从而超越了这些传统技术，就像我们在本小节和人工智能部分介绍的那样。
      <br/>
      <br/>
      控制系统分为两类:(a)纵向控制:调节车辆巡航速度;(b)横向控制:转向车辆车轮进行路径跟踪。
    </div>
  </article>
  <div class='pic'>
      <img id="深度学习传统控制算法比较" src='../../assets/dlkzsfbj.png' width="900" height="1000">
      <p>表：深度学习传统控制算法</p>
  </div>

  <article class="message is-link">
    <div id="深度车辆横向控制"  class="message-body">
      <b>4.4.1 车辆横向控制</b>
      <br/>
      <br/>
      横向控制可以选择所需的转向角度，并纠正驾驶过程中可能累积的任何误差。
      <br/>
      <br/>
      Rausch等人(2017)提出了一种基于cnn的转向角预测方法。他们用自动驾驶汽车摄像头收集的数据来训练他们的方案。这种方法已经在只有双车道道路没有交通的情况下进行了评估。
      <br/>
      <br/>
      Sharma等人(2018)提出了一种在不同轨道(例如，多车道轨道和单车道未知轨道)上引导自动驾驶汽车的方法。他们使用cnn和TORCS (Wymann et al.， 2000)用从传感器收集的数据(即速度、转向角、油门和刹车位置)来训练和测试他们的方案。仿真结果表明，他们的模型能够成功地控制汽车，并在不越过车道标记的情况下完成整个圈速。但是，他们的方法在TORCS模拟器上只能在两条轨迹上进行仿真。
      <br/>
      <br/>
      Lee和Ha(2020年)利用CNNs和LSTM来预测自动驾驶汽车的转向角。cnn用于提取驾驶过程中摄像头图像的特征。将这些特征反馈到LSTM中，进行转向角估计。
      <br/>
      <br/>
      Maqueda等人(2018)解决了在不同白天时段(如白天和夜晚)估计转向角的问题。他们利用He等人(2016)使用的cnn来训练来自事件摄像机的数据(Lichtsteiner等人，2008)。
      <br/>
      <br/>
      Yang等人(2018b)报告称，仅预测转向角度不足以实现车辆控制。因此，他们建议估计包括速度控制的参数。他们使用带有5个卷积层和4个全连接层的cnn从Udacity模拟器(公共驾驶数据集，2017)的数据中提取特征。他们使用的训练数据集包括速度值、转向角度和三个前视摄像头的视频流。将提取的特征输入LSTM，预测舵机的转角和速度指令。
      <br/>
      <br/>
      Chen等人(2019c)提出了使用Bojarski等人(2016)和LSTM中的cnn学习驾驶车辆的辅助任务网络(ATN)方法。他们训练他们的网络架构，根据相机的图像输入来预测转向角度。他们使用Udacity模拟器和Comma.ai dataset来评估他们的提案。
      <br/>
      <br/>
      Mújica-Vargas等人(1062)研究了一种增强的司机混合驾驶方法，以在不同的情况下(例如，变道、红绿灯、交通标志和从一条路转到另一条路)引导自动驾驶汽车。他们的建议结合了cnn和rnn，其中cnn提取了从Udacity模拟器收集的数据的特征。但是，他们没有考虑到障碍物和其他道路使用者。
      <br/>
      <br/>
      Kebria等人(2019)提出了一种基于cnn的架构，以减少训练时间并预测自动驾驶汽车的转向角。他们使用cnn来提取驾驶过程中记录的图像的重要特征。他们得出的结论是，在第一层拥有较大过滤器的cnn架构具有出色的性能。
      <br/>
      <br/>
      Yang等人(2018a)使用TORCS (Wymann等人，2000)和CARLA (Dosovitskiy等人，2017)模拟器进行数据收集，而不是包括不同的条件(如白天和夜晚)。这些数据被输入到cnn来估计自动驾驶汽车的转向角。然而，作者没有考虑其他具有挑战性的场景(例如，十字路口和密集的交通)。
    </div>
  </article>
  <div class='pic'>
