  </div>

  <article class="message is-link">
    <div id="深度车辆纵向控制"  class="message-body">
      <b>4.4.2 车辆纵向控制</b>
      <br/>
      <br/>
      纵向控制负责自动引导，在驾驶过程中调节自动驾驶汽车的速度，保证安全舒适。由于DL可以根据在真实世界或仿真环境中收集的标记训练数据来估计正确的速度，因此它已成为一种流行的纵向控制方法。
      <br/>
      <br/>
      Mohseni等人(2018)提出了一种模型预测控制(MPC)方法来调节AVs的速度以避开障碍物。他们用从激光雷达传感器收集的数据(包括位置、大小和速度信息)来训练DNN架构。此方案的目标是在障碍物出现的地方自定义速度。
      <br/>
      <br/>
      Szilassy等人(2019)提出了一种利用DNN估计路口AVs速度的控制方法。DNN以AVs的位置和初速度作为输入。然而，他们的建议没有考虑十字路口的其他关键参数(如行人行为)。
      <br/>
      <br/>
       al-sharman等人(2020)提出了一种基于DNN架构的状态估计方法。他们使用真实的车辆来收集数据(车辆状态、动力系统状态和制动值)来模仿人类驾驶员。并且将数据输入DNN进行制动压力预测。
      <br/>
      <br/>
       Yang等人(2018a)使用TORCS (Wymann等人，2000)和CARLA (Dosovitskiy等人，2017)模拟器进行数据收集，而不是包括不同的条件(如白天和夜晚)。这些数据被输入到cnn来估计自动驾驶汽车的转向角。然而，作者没有考虑其他具有挑战性的场景(例如，十字路口和密集的交通)。
    </div>
  </article>
  <div class='pic'>
      <img id="深度学习横向纵向算法比较" src='../../assets/dlzxkzsfbj.png' width="900" height="1000">
      <p>表：深度学习纵向控制算法</p>
  </div>

  <article class="message is-link">
    <div id="深度车辆横向纵向控制"  class="message-body">
      <b>4.4.3 车辆横纵向同时控制</b>
      <br/>
      <br/>
      前面的章节表明DNN可以训练为车辆的纵向或横向控制。为了改善转向和加速机制，纵向和横向控制可以同时使用。在下面的文章中，我们将简要讨论使用这两个控件的方法。
      <br/>
      <br/>
      Chen等人(2015)提出了一种估计控制指令(转向、加速和制动)的方法。他们用TORCS(2021年)收集的12小时视频数据训练cnn。他们的提议使自动驾驶汽车能够在不同的场景下驾驶。Devineau等人(2018)提出了一种多层感知(MLP)方法，利用cnn训练自动驾驶汽车在具有挑战性的情况下驾驶(包括在长直线和紧密曲线之间切换)。实验结果表明，该方法对舵机转角的估计是有效的。
      <br/>
      <br/>
      Sharma等人(2019年)使用TORCS(2021年)收集数据(包括按速度、转向角、油门和刹车位置标记的道路图像)。这些数据被提供给cnn，训练模型同时学习预测车辆速度和转向角度。
      <br/>
      <br/>
       Xing等人(2020)开发了一种在高速公路上变道的方法。虽然该方案在训练阶段同时使用了CNNs和rnn，但尚未在车流量密集的关键情况下进行测试。
    </div>
  </article>
