  </div>
  </div>


  <article class="message is-link">
    <div id="深度学习车辆社会行为"  class="message-body">
      <b>4.5 社会行为</b>
      <br/>
      <br/>
      汽车行业的目标是在未来几年推出五级(全自动化)自动驾驶汽车。在未来的交通系统中获得完全的自动驾驶汽车，这都将取决于自动驾驶汽车如何识别路人的行为。DL技术在理解路人的意图，从而做出及时的交通决策方面显示出良好的结果。
      <br/>
      <br/>
      许多技术都被广泛用于理解行人在道路上的行为，无论是通过理解行人的运动还是分析行人的行为和意图。
      <br/>
      <br/>
      Hoermann等人(2018)将贝叶斯滤波技术和cnn结合起来，与道路用户(包括行人、自行车和车辆)训练数据。他们的建议的目的是估计每个道路使用者的意图。
      <br/>
      <br/>
      Saleh等人(2020)提出了在交通环境中理解行人意图的问题。他们仅基于来自摄像机的视频，使用黄等人(2017)的DenseNets实时检测和跟踪行人。该方法在意图预测方面取得了良好的结果。
      <br/>
      <br/>
      Abughalieh和Alawneh(2020)提出了一种基于cnn和收集的图像预测行人意图的方法。他们的提案允许在人行横道上识别行人的方向和与自动驾驶汽车的距离。然而，他们没有实时考虑行人的姿势。
      <br/>
      <br/>
      Morales Alvarez et al. (2020) 利用自动驾驶汽车中传感器获取的数据来识别行人的位置(例如姿势和位置)。他们利用这些信息，根据rnn估计行人过马路的意图。结果显示，75%的测试在意图预测方面是成功的。
      <br/>
      <br/>
      Muscholl等人(2020)使用贝叶斯网络来理解行人过马路时的行为。结果表明，他们的建议比其他现有方法更好地估计行人位置(Gupta等人，2018年;黄等人，2019a;Mohamed等人，2020年)。
      <br/>
      <br/>
      Poibrenski等人(2020年)使用rnn估计道路上每个行人的所有可能位置。对网络进行训练，根据车辆摄像头收集的图像预测行人的位置。
      <br/>
      <br/>
      Elallid等人(2022)提出了一个使用P-LPN技术检测和统计避难岛上行人的模型。随着时间的推移，数字编号会被更新，并使用LSTM预测AVs的决策。这取决于等待过马路的行人数量。如果车辆数量较多，自动驾驶汽车需要刹车以给行人通过的机会，否则自动驾驶汽车会加速。
    </div>
  </article>
