  <article class="message is-link">
    <div id="深度学习车辆通信"  class="message-body">
      <b>4.6 通信</b>
      <br/>
      <br/>
      通信技术对提高自动驾驶车辆的感知能力和规划能力以及实现更好的车辆控制有着有效的影响。在某些情况下，传感器不能提供准确的信息。在这些情况下，通信可以用来在车辆之间共享信息。
      <br/>
      <br/>
      信道状态信息是无线通信中的一个术语，用来描述通信链路的信道参数。这个信息代表了散射、衰落和功率随距离衰减的累积效应，描述了信号如何从发射机传播到接收机。
      <br/>
      <br/>
      Du et al.(2017)提出了一种解决大面积资源分配问题的LSTM方法。他们收集了24小时的数据并进行了标记。根据历史交通数据，他们的建议可以检查一段时间内的交通量。
      <br/>
      <br/>
      Li等人(2019b)提出通过共享车辆和基础设施之间的实时数据来提高V2I通信的可靠性。他们使用DNN来加速处理如此大量的数据。测试结果表明，该方法在密集流量下具有良好的性能。
      <br/>
      <br/>
      Gao等人(2019)提出了一种解决V2X通信功率分配问题的方法。利用DNN实时提供资源分配解决方案。Chen等人(2019b)利用cnn来训练来自V2I和V2V通信的数据，解决了同样的问题。他们得出的结论是，与DNN架构相比，cnn降低了网络的权重，从而加快了训练过程。
      <br/>
      <br/>
      Liu等人(2019a)使用LSTM训练数据(包括历史CSI)，以预测未来CSI，用于管理资源分配。他们将数据归一化，以加速网络权值的优化，并迅速找到最优解。在通道预测方面，该结果优于Xu等人(2019)的方法。
      <br/>
      <br/>
      Zhang等人(2020d)使用DNN处理GPS数据，即每辆车的时间戳、纬度和经度信息。这些数据被提供给DNN来预测CSI。
    </div>
  </article>
